import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import NormalizationSections from '../views/NormalizationSections';
import NormalizationDepo from '../views/NormalizationDepo';
import NormalizationRatio from '../views/NormalizationRatio';
import RegulationJoints from '../views/RegulationJoints';
import RegulationSections from '../views/RegulationSections';
import RegulationAnalysis from '../views/RegulationAnalysis';
import Normalization from '../views/Normalization';
import Regulation from '../views/Regulation';
import Planning from '../views/Planning';
import PlanningTechStationTasks from '../views/PlanningTechStationTasks';
import PlanningJointsTasks from '../views/PlanningJointsTasks';
import PlanningMain from '../views/PlanningMain';
import PlanningIndicators from '../views/PlanningIndicators';
import PlanningMidLocoCrew from '../views/PlanningMidLocoCrew';
import PlanningStatistics from '../views/PlanningStatistics';
import PlanningGraf from '../views/PlanningGraf';
import Modeling from '../views/Modeling';
import ModelingSections from '../views/ModelingSections';
import ModelingDepo from '../views/ModelingDepo';
import ModelingRatio from '../views/ModelingRatio';

Vue.use(VueRouter);

const routes = [
  {
    path: '/tasks/',
    name: 'Home',
    component: Home
  },
  {
    path: '/norm/',
    component: Normalization,
    children: [
      {
        path: 'section',
        component: NormalizationSections
      },
      {
        path: 'depo',
        component: NormalizationDepo
      },
      {
        path: 'ratio',
        component: NormalizationRatio
      }
    ]
  },
  {
    path: '/plan/',
    component: Planning,
    children: [
      {
        path: 'st_tasks',
        component: PlanningTechStationTasks
      },
      {
        path: 'join_tasks',
        component: PlanningJointsTasks
      },
      {
        path: 'plan_main',
        component: PlanningMain
      },
      {
        path: 'lc_task',
        component: PlanningIndicators
      },
      {
        path: 'lc_plan',
        component: PlanningMidLocoCrew
      },
      {
        path: 'plan_ind',
        component: PlanningStatistics
      },
      {
        path: 'graf_loco',
        component: PlanningGraf
      }
    ]
  },
  {
    path: '/reg/',
    component: Regulation,
    children: [
      {
        path: 'joint',
        component: RegulationJoints
      },
      {
        path: 'section',
        component: RegulationSections
      },
      {
        path: 'analysis',
        component: RegulationAnalysis
      }
    ]
  },
  {
    path: '/model/',
    component: Modeling,
    children: [
      {
        path: 'section',
        component: ModelingSections
      },
      {
        path: 'depo',
        component: ModelingDepo
      },
      {
        path: 'ratio',
        component: ModelingRatio
      },
      {
        path: 'nsi',
        component: RegulationAnalysis
      },
      {
        path: 'compare',
        component: RegulationAnalysis
      }
    ]
  }
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
});

export default router;
