<template>
  <v-card
    class="detail-block"
    elevation="0"
    :class="{
      'slide-leave slide-enter-active': this.$store.state.sheet,
      'slide-leave-to slide-leave-active': !this.$store.state.sheet
    }"
  >
    <v-col class="pa-0">
      <v-btn
        color="#00194D"
        class="open-btn"
        dark
        @click="openSheet()"
        block
        height="20"
        elevation="0"
        :ripple="false"
      >
        <v-icon
          class="icon icon-arrow-dropdown"
          :class="{ 'icon-arrow-dropdown-up': this.$store.state.sheet }"
        ></v-icon>
      </v-btn>
      <v-row
        class="detail-header"
        :class="{ 'is-opened': $store.state.sheet }"
        @mousedown.left.prevent="mouseDownLeft($event)"
      >
        <h3 class="detail-title mr-auto">
          {{ this.$store.state.detailInfo.detailName }}
        </h3>
        <transition name="fade">
          <PrintButton
            class="mr-4"
            v-if="this.$store.state.sheet && tableType !== 'graf_loco'"
          />
        </transition>
        <transition name="fade">
          <ExportButton
            v-if="this.$store.state.sheet && tableType !== 'graf_loco'"
          />
        </transition>
      </v-row>
      <transition name="slide">
        <v-card class="detail-table">
          <DataTable
            v-if="tableType !== 'graf_loco'"
            :headers="headers"
            :type="tableType"
            :params="params"
            :isDetail="true"
            ref="tableDetail"
            :isRemote="isRemote"
          />
          <div v-else class="svg-legend"></div>
        </v-card>
      </transition>
    </v-col>
  </v-card>
</template>

<script>
import DataTable from './DataTable';
import PrintButton from './PrintButton';
import ExportButton from './ExportButton';
import * as d3Selection from 'd3-selection';

const d3 = Object.assign({}, d3Selection);

export default {
  name: 'DetailPanel',
  components: { ExportButton, PrintButton, DataTable },
  props: ['headers', 'url', 'params', 'tableType', 'isRemote'],
  methods: {
    created() {
      if (this.tableType === 'graf_loco') {
        this.drawLegend();
      }
    },
    drawLegend() {
      d3.select('.svg-legend').selectAll('svg').remove();

      // let legend = d3.select('.svg-legend').append('svg').attr('width', 1980).attr('height', 150);
    },
    openSheet() {
      this.$store.state.sheet = !this.$store.state.sheet;
    },
    updateDetail(url) {
      let prm = {};
      if (this.isRemote) prm = { load: false };
      this.$refs.tableDetail.fetchTableData(prm, url);
    },
    mouseDownLeft(e) {
      if (this.$store.state.sheet) {
        this.$store.state.resizing = true;
        this.$store.state.mouseY = e.y;
      }
    }
  },
  mounted() {
    let tables = document.querySelectorAll('.table');
    window.tableContent = tables[0];
    window.tableDetail = tables[1];
  }
};
</script>

<style lang="sass">
.fade-enter, .fade-leave-to
  opacity: 0
.slide-enter, .slide-leave-to
  height: 59px
  overflow: hidden
.slide-enter-to, .slide-leave
  overflow: hidden

.detail-block
  border-radius: 4px
  .open-btn
    z-index: 1
    &:hover
      background-color: #30446E
  .detail-table
    overflow: hidden
  .detail-header
    position: relative
    background-color: $color-blue
    color: $color-white
    padding: 14px 10px 9px 40px
    font-family: $font-bold
    margin: -5px 0 0
    border-radius: 0 0 4px 4px
    &.is-opened
      cursor: row-resize
    .detail-title
      font-size: 16px
      line-height: 22px
    .print, .export
      color: $color-white
    .print::after, .export::after
      background-color: $color-white
  .icon-arrow-dropdown
    transform: rotate(180deg) translateY(0px)
    margin: 0 auto
    font-size: 15px
    transition: transform $transition-normal
    &-up
      transform: rotate(0deg) translateY(1px)
</style>
